@import '~antd/dist/antd.css';
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.backgroundHome {
  background-color: '#B61624';
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.name {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 2.5em;
}
.bread {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 2.5em;
}

.hi {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 2.5em;
}

.title {
  font-family: 'Arya', sans-serif;
  font-size: 7em;

}


.titleName {
  font-family: 'Arya', sans-serif;
  font-size: 7em;

}

.screenMenu {
  display: none;
}

.prof {
  width:100px;
  height:100px;
}
.aboutText {
  font-size: 20px;
}



@media (min-width: 421px) {
  .hi {
    font-family: 'Noto Sans JP', sans-serif;
    font-size:2.5em;
    margin-top:1em
  }
  .screenRow {
    display:none
  }


  .mainIcon {
    margin-right:30px;
  }
  .faicon {
    margin-right: 25px;
  }
  .cardBod {
    height:150px;
  }
  .prof {
    width:200px;
    height:200px;
  }
  .title {
    font-family: 'Arya', sans-serif;
    font-size: 7em;
  
  }
  .titleName {
    font-family: 'Arya', sans-serif;
    font-size: 7em;
  
  }

  .faicon {
    margin-right: 30;
    cursor: pointer;
  }

  .iconCol {
   display: flex;
   margin-right: auto;
   
  }
  .nameScreen {
    display:none;
  }
  .nameReg {
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 2.5em;
  }

  .projCard {
    width:25em;
    height: 32em
  }
  .resumeFont {
    font-size: 2em;
  }
  .menuScreen {
    display:none
  }



}

@media  (max-width: 420px) {

  .projCard {
    width: 12em;
   
  }

  .cardBod {
    height: 20em;
  }
  .resumeFont {
    font-size: 1em;
  }

  .nameScreen {
    text-align: center;
    font-size:0.75em;
  }
  .screenRow {
    /* margin-top:-50em; */
    /* width:10em; */
    text-align: center;

  }
  .projRow {

    text-align: center;

  }
  .nameReg {
    display:none
  }
  .screenMenu {
    display: block;
    text-align: center;
    font-size: 1.2em;
    color: black;
  }
  .nonscreen{
    display: none;
  }
  .aboutText {
    font-size: 10px
  }
  .prof {
    margin-top: 10px;
  }

  .menuScreen {
    font-size: 0.3em;
    text-align: center;

    margin-bottom: 30px
  }

  .title {
    font-size: 1.7em;
    text-align: center;

  }
  .titleName {
    font-size: 1.7em;
    text-align: center;
    padding-top: 30px

  }
  .mainIcon {
    /* margin-left: -5px; */
    font-size: 3.2em;
    /* margin-top: -50px; */
    text-align:center;
    cursor: pointer;
    padding-right: 20px
  }

  .iconCol {
    /* display: flex; */
    text-align: center;
    
   }

  .name {
    font-size: 0.7em;

  }
  .resp {
    display: table-cell;

    /* vertical-align: middle; */

    /* width:50%; */
  }
  .hi {
    display: none;
  }

  .nonSR {
    display: none;

  }

  .faicon {
    font-size: 3em;
    margin-right: 10px;
    cursor: pointer;
  }
}

@media (min-width: 450px) and (max-width: 768px) {
  .nonscreen {
    font-size: .7em;
  }

  .titleName {
    font-size: 5em;
    margin: 0 auto;
    display: inline;    

    text-align: center;

  }
  .nameReg {
    font-size: 2em;
    text-align: center;

  }


}

@media (width: 320px) {
  .mainIcon {
    /* margin-left: -5px; */
    font-size: 2.5em;
    /* margin-top: -50px; */
    text-align:center;
    cursor: pointer;
    padding-right: 10px
  }

  .screenRow {
    width: 9em
  }

}


